
import { Mod } from '@/store/mod';
import { vms } from '@/type/vms';
import { fns } from '@hyong8023/tool-box';
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

@Options({
  components: {},
  emits: [],
})
export default class Linker extends Vue {
  @Prop({ required: true }) label!: string;
  @Prop({ required: true }) link!: string;

  @Mod.sys.Action('refer') outerRefer!: fns.Consume<vms.OuterRefer>;

  onClick() {
    this.outerRefer({
      type: 'REFER',
      href: this.link,
      label: this.label,
    });
  }
}
